@import './common/variables';

.App {
  text-align: center;
  height: 100%;
  min-height: 100vh;
}

.body {
  background-color: $bodyBckgColor;
  width: calc(100vw - 220px);
}

.App-body {
  margin: 20px 0;
}

.main-page {
  margin: 50px;
}

.error-text {
  color: $errorColor;
  font-size: 18px;
}

.success {
  color: $primaryColor;
  font-size: 18px;
  margin: 50px;
}

a {
  text-decoration: none;

  button {
    color: $primaryColor;
    border: 1px solid transparent;
    border-radius: 10px;
    padding: 5px;

    &:hover {
      border-color: $primaryColor;
    }
  }
}

.back-button {
  margin: 20px 0 0 20px;
  color: $primaryColor;
}


.loading {
  display: inline-block;
  width: 80px;
  margin: auto;
  height: 80px;

  &:after {
    content: '';
    display: block;
    width: 64px;
    height: 64px;
    margin: 8px;
    border-radius: 50%;
    border: 6px solid;
    border-color: $primaryColor transparent $primaryColor transparent;
    animation: rotating 1.2s linear infinite;
  }
}

@keyframes rotating {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}