@import './common/variables';

.top-buttons,
.header {
  border-bottom: 2px solid $borderColor;
  padding: 15px;
  height: 30px;
}

.header {
    text-align: right;

  .avatar {
    width: 30px;
    height: 30px;
  }

  h3 {
    margin: 0 5px;
  }
}

.search {
  margin: 20px;
  text-align: left;

  .search-input {
    border: 1px solid $primaryColor;
    border-radius: 10px;
    padding: 2px 10px;
    width: 150px;
    margin-right: 10px;
  }
}

.search-by {
  color: $primaryColor;
  font-size: 16px;
  font-weight: bold;
  margin-right: 10px;
}
