@import './common/variables';

.cocktails-page {
    .cocktails-list {
        display: flex;
        flex-wrap: wrap;
    }
}

.cocktails-list {
    width: 100%;
    max-height: calc(100vh - 250px);
    min-height: 250px;
    overflow: auto;
    padding: 0 20px;
    align-items: center;
    margin: 50px auto 0;
}

.cocktail-image {
    img {
        width: 100px;
        height: auto;
        border-radius: 10px;
    }
}

.cocktail-wrapper {
    margin: 10px;
    cursor: pointer;
    width: 300px;
    border: 1px solid transparent;
    border-radius: 10px;
    padding: 5px;
    &:hover {
        border-color: $primaryColor;
    }
}

.cocktail-info {
    margin-left: 10px;
}

.cocktail-name {
    font-size: 20px;
    color: $textColor;
    margin-bottom: 10px;
}

.cocktail-ingredients {
    font-size: 14px;
    color: $textColor;
}

.tag {
    background: $primaryColor;
    color: white;
    padding: 15px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    border-bottom-left-radius: 10px;
    position: absolute;
    top: 0;
    right: -1px;
}

/* Categories */
.categories {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 150px;
    min-height: 0;
    border-color: $borderColor;

    .cocktail-wrapper:hover {
        background-color: $primaryColor;
        transition: 0.3s all ease-in;

        &:hover {
            .cocktail-name {
                color: $white;
            }
        }
    }
}

.info-section {
    margin: 20px;
    padding: 20px;
    border: 1px solid $primaryColor;
    border-radius: 10px;
    cursor: pointer;
    position: relative;
    max-width: 600px;

    &:hover {
        background: $sideBarBckgColor;
    }

    img {
        width: 250px;
        height: 250px;

        &.small-img {
            width: 50px;
            height: auto;
        }
    }
}

.body {
    .tab-header {
        background-color: #46B04A;
    }
}