@import "./variables";

.flex {
  display: flex;
}

.flex-center {
  align-items: center;
  justify-content: center;
}

.flex-column {
  flex-direction: column;
}

.flex-wrap {
  flex-wrap: wrap;
}

.flex-start {
  justify-content: flex-start;
}

.flex-align-start {
  align-items: flex-start;
}

.flex-space-between {
  justify-content: space-between;
}