@import './common/variables';

.sidebar {
    background-color: $sideBarBckgColor;
    width: 220px;
}

.list {
    display: flex;
    flex-direction: column;
    align-content: flex-start;
    justify-content: center;
    text-align: left;
}

.main-menu {
    margin: 20px;
}

.list {
    a {
        padding: 10px;
        color: $textColor;
        text-decoration: none;

        &:hover,
        &:active,
        &.active {
            color: $primaryColor;
            background-color: $bodyBckgColor;
            border-radius: 10px;
            text-decoration: none;
        }

    }
}